import { InjectionKey } from "vue";
import { createStore, Store } from "vuex";
import { Pattern } from "@/modules/CGOL";

export const key: InjectionKey<Store<State>> = Symbol();

export type State = {
  pattern: Pattern;
  gen: number;
  isLoaded: boolean;
};

export const GetterTypes: {
  Pattern: "Pattern";
  Gen: "Gen";
  isLoaded: "isLoaded";
} = {
  Pattern: "Pattern",
  Gen: "Gen",
  isLoaded: "isLoaded",
};

export const MutationTypes: {
  UpdatePattern: "UpdatePattern";
  UpdateGen: "UpdateGen";
  SetLoaded: "SetLoaded";
} = {
  UpdatePattern: "UpdatePattern",
  UpdateGen: "UpdateGen",
  SetLoaded: "SetLoaded",
};

export const store = createStore<State>({
  state: {
    pattern: "random",
    gen: 0,
    isLoaded: false,
  },
  getters: {
    [GetterTypes.Pattern](state) {
      return state.pattern;
    },
    [GetterTypes.Gen](state) {
      return state.gen.toString();
    },
    [GetterTypes.isLoaded](state) {
      return state.isLoaded;
    },
  },
  mutations: {
    [MutationTypes.UpdatePattern](state, pattern: Pattern) {
      state.pattern = pattern;
    },
    [MutationTypes.UpdateGen](state, gen: number) {
      state.gen = gen;
    },
    [MutationTypes.SetLoaded](state, isLoaded: boolean) {
      state.isLoaded = isLoaded;
    },
  },
  actions: {},
  modules: {},
});
